   mtb* 上下 例:mtb10 上下加10px
   mlr* 左右 例:mlr10 左右加10px
   mt*  上 例：mt10 上加10px
   mb*  下 例：mb10 下加10px
   ml*  左 例：ml10 左加10px
   mr*  右 例：mr10 右加10px

   size*  字体大小 例：size15  = font-size:15px
   
   bold 字体加粗
   
   w_*  百分比宽度  例:w_5 = width:5%
